.factsheet-edit-page {
  height: 100%;
}
.factsheet-edit-page .SplitPane {
  height: calc(100% - 50px);
}
.factsheet-edit-page .rightlayout,
.factsheet-edit-page .rightPannel {
  height: 100%;
}
.factsheet-edit-page .SplitPane.vertical {
  height: calc(100% - 50px) !important;
}
.factsheet-edit-page .factsheetWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0px;
  height: 100%;
}
